.menu {
  display: none;
  
  @media (max-width: $tablet-width) {
    background-color: $white-color;
    max-width: 376px;
    // position: absolute;
    z-index: 11;
    padding: 0 20px;
    left: 0;
    transition: all .1 ease;
    position: fixed;
    // top: 0;  
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    z-index: 9999;
  }
}

.menu-santeh {
  display: none;

  @media (max-width: $tablet-width) {
    max-width: 376px;
    min-width: 320px;
    background-color: $white-color;
    max-width: 376px;
    position: absolute;
    z-index: 111;
    padding: 0 20px;
    left: 0;
    transition: all .1 ease;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
  }
}

.menu__top {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  margin: 60px 0;
  padding-top: 20px;
  border-top: 1px solid $black-color;

  &--santeh {
    margin-bottom: 23px;
  }
}

.menu__picture {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.menu__name {
  margin-top: 12px;
}

.menu__nav-item {
  &:not(:last-child) {
    margin-bottom: 20px;
    transition: all 0.3s ease-in-out;
  }
}

.menu__nav {
  padding-top: 20px;
  border-top: 1px solid $black-color;
  margin-bottom: 70px;
}

.menu__bottom  {
  padding-top: 20px;
  border-top: 1px solid $black-color;
  margin-bottom: 20px;
}

.menu__lang-name {
  text-transform: uppercase;
}

.menu__santeh-link {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
}

.menu__santeh  {
  position: relative;
}

.menu__tile-link {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
}

.menu__tile {
  position: relative;
}

